.List{
    color:var(--secondaryColor);
    border-bottom: 2px solid transparent;
    padding: 8px;
    margin:0 12px;
    transition: border-bottom 300ms linear 100ms;
}
.List:last-child{
    margin: 0;
    background-color: transparent;
    border: 2px solid red;
    border-radius: 24px;
    width: 120px;
    text-align:center;
}
.List:active, .List.active{
    border-bottom-color: var(--primaryColor);
    color:var(--primaryColor);
    font-weight: 700;
   
}
.List:last-child:hover, .List:last-child:active, .List:last-child.active{
    margin: 0;
    background-color: rgb(255,0,0);
    border: 2px solid var(--primaryBColor);
    border-radius: 24px;
    width: 120px;
    text-align:center;
    font-weight: 700;
}
.List a{
    text-decoration: none;
    color:inherit;
    font-size: 0.9rem;
}
.List:hover{
    color:var(--primaryColor);
    
}
 @media (max-width: 600px) {
     .List {
         width: 90%;
         margin: auto;
         border-bottom: 0px;
         padding-right: 42px;
     }
    .List a{
        font-size: 3.3rem;
    }
    .List:last-child{
        
        background-color: transparent;
        border: 0px;
        width: 90%;
        margin: auto;
        text-align: right;
    }
    .List:last-child:hover, .List:last-child:active, .List:last-child.active{
        background-color: transparent;
        border: 0px;
        width: 90%;
        margin: auto;
        text-align: right;
        color: var(--primaryColor);
        font-weight: 700;
    }
    
 }
