.Contact{
    width: 100%;
    padding: 5% 10%;
    background-color: var(--primaryBColor);
    color:var(--secondaryBColor)
}
.Contact h3{
    width: 100%;
    text-align: left;
    padding: 36px 0px;
    display: inline-block;
    font-size: 2.4rem;
    font-weight: 700;
  


}
 .Contact h3::after{
    content: '';
    width: calc(100% - 160px);
    min-height: 2px;
    background-color: var(--secondaryBColor);
    border-radius: 4px;
    display: inline-block;
    margin-bottom: 8px;
}

.Contact h3::after{
    margin-right: 0;
    margin-left: 9px;
}

.Grid{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}
.Online{
    width: 40%;
    font-size: 3.2rem;
}
.Online p{
    font-size: 1.2rem;
    margin-bottom: 16px;
    display: flex;
    align-items: center;
}
.Call{
    display: inline-block;
    margin-left: 12px;
}
.Form{
    width:55%;
}
.Form form{
    width: 100%;
}
.Form button{
    width: 240px;
    margin: 12px auto;
    text-align: center;
    padding: 12px;
    border-radius: 24px;
    border: 1px solid var(--primaryColor);
    background-color: var(--primaryBColor);
    color: var(--primaryColor);
    margin-left: 28px;
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
    letter-spacing: 2px;
}
.Form button:hover{
    color: var(--primaryBColor);
    background-color: var(--primaryColor);
    border-color: var(--primaryBColor);
}
.Form button:disabled{
    border: 1px solid var(--secondaryBColor);
    color: var(--secondaryBColor);
}
.Form button:disabled:hover{
    border: 1px solid var(--secondaryBColor);
    color: var(--secondaryBColor);
}
.Form p{
    width: 100%;
    padding: 8px;
    border-radius: 8px;
    animation: fadeAway 500ms linear 5s 1 ;
    text-align: center;

}
.Good{
    color:var(--primaryColor);
    border: 1px solid var(--primaryColor);
}
.Error{
    color: red;
    border: 1px solid red;
    background-color: rgba(200,0,0,0.2);
}
@keyframes fadeAway {
    from{
        width: 100%;
        opacity: 1;

    }
    to{
        width: 0;
        opacity: 0;
    }
}
@media (max-width:600px) {
    .Contact{
        padding: 5% 24px;
    }
    .Grid{
        flex-direction: column;
    }
    .Online{
        width: 100%;
    }
    .Online .Grid{
        flex-direction: row;
    }
    .Form{
        width: 100%;
    }
    .Form button{
        width: 170px;
    }
}