.Work{
    padding: 120px 10%;
    min-height: 60vh;
    width: 100%;
}

.Work h3{
    width: 100%;
    text-align: left;
    padding: 36px 0px;
    display: inline-block;
    font-size: 2.4rem;
    font-weight: 700;
  


}
 .Work h3::after{
    content: '';
    width: calc(100% - 128px);
    min-height: 2px;
    background-color: var(--primaryBColor);
    border-radius: 4px;
    display: inline-block;
    margin-bottom: 8px;
    margin-right: 0;
    margin-left: 9px;
}

@media (max-width: 600px) {
    .Work{
        padding: 120px 24px;
    }
}

