.Skill{
    display: flex;
    width: 30%;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    color: var(--secondaryBColor);

}
.Skill>*{
    margin-bottom: 12px;
}
.ImgContainer{
    width: 80px;
    height: 80px;
    overflow:hidden;
    
}
.ImgContainer img{
    max-width: 100%;
    width: 100%;
    max-height: 100%;
    color: var(--secondaryBColor);
}
.Title, .Content{
    font-size: 1.2rem;
    width:100%;
    text-align: center;
    color:inherit;
    font-weight: 700;
}
.Content{
    font-size: 0.9rem;
    font-weight: 400;

}
@media (max-width:600px) {
    .Skill{
        margin-bottom: 34px;
        width: 100%;
    }
}