.Section{
    padding: 140px 10%;
    min-height: 60vh;
    width: 100%;
    color: var(--primaryBColor)
}
.Section h3{
    width: 100%;
    text-align: left;
    padding: 36px 0px;
    display: inline-block;
    font-size: 2.4rem;
    font-weight: 700;
  


}
 .Section h3::after{
    content: '';
    width: calc(100% - 120px);
    min-height: 2px;
    background-color: var(--primaryBColor);
    border-radius: 4px;
    display: inline-block;
    margin-bottom: 8px;
    margin-right: 0;
    margin-left: 9px;
}

.Letter{
    width:50%;
}
.Section .Letter>p{
    margin-bottom: 1rem;
    font-size: 1rem;
    margin-block-start: 1em;
    margin-block-end: 1em;
    width: 100%;
    text-align: justify;
   
}
.Grid{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.Profile{
    background-color: var(--primaryBColor);
    background-size: cover;
    background-blend-mode: luminosity;
    min-height: 460px;
    width:40%;
    border-radius: 50%;
    filter:grayscale(100%);
    
    
}
@media (max-width:600px) {
    .Section{
        padding: 140px 24px;
    }
    .Profile{display: none;}
    .Letter{
        width: 100%;
    }
}