.Page{
    background-color: var(--primaryBColor);
    background-blend-mode: darken;
    width:100%;
    background-size: 33vw;
    color: var(--secondaryBColor);
    padding-top: 1px;
    animation: pageUp 1.1s linear 9s 1 normal both ;
    overflow: hidden;
    max-height: 100vh;

}
@keyframes pageUp{
    from{
           height: 100vh;
        
    }
    30%{
       height: 80vh;
    }
    70%{
        height: 80vh;
    }
    95%{
        opacity: 1;
    }
    to{
      
        height: 0px;
        opacity: 0;
    }
} 
.Center{
    display: flex;
    justify-content: flex-start;
    height: 30vh;
    margin: 35vh auto;
    width: 50%;


}
.Bar{
    width: 5px;
    height: 100%;
    background-color: var(--primaryColor);
    transform: rotate(0deg);
    animation: rot720 700ms linear 100ms 1 ;
   
}
.Title{
    margin: 30px 35px;
    height: 100%;
    
    
    
}
.Title h2{
    font-size: 2.2rem;
    width:0;
    border-right: 1px solid var(--primaryBColor);
    line-height: 4.5rem;
    white-space: nowrap;
    overflow:hidden;
    animation: typewritter1 3s steps(40) 1.1s 1 normal both,
        blinkTextCursor 400ms steps(40) 1.1s 6 normal;
}
.Title p{
    font-size: 1.4rem;
    width:0;
    border-right: 1px solid var(--primaryBColor);
    white-space: nowrap;
    overflow:hidden;
    animation: typewritter2 3s steps(40) 4.5s 1 normal both,
        blinkTextCursor 400ms steps(40) 4.5s 10 normal;
}

@keyframes rot720 {
    from{
        transform: rotate(0deg);
    }
    30%{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}
@keyframes typewritter1 {
    from{
        width:0;
       
    }
    to{
        width:28rem;
    }
}
@keyframes typewritter2 {
    from{
        width:0;
       
    }
    to{
        width:16rem;
    }
}
@keyframes blinkTextCursor {
    from{
        border-right-color: var(--secondaryBColor);
    }
    to{
        border-right-color: transparent;
    }
}