.Icon{
    width: 60px;
    max-height: 60px;
    border-radius: 8px;
    margin: 12px;
    margin-left: 0;
    transform: scale(1.0,1.0);
    cursor: pointer;
    border:2px solid var(--secondaryBColor);
    padding: 8px;
    padding-top: 1px;
    padding-bottom: 60px;
    text-align: center;
    display: inline-block;
}
.Icon:hover{
    transform: scale(1.1,1.1);
}
.Icon img{
    max-width: 100%;
    max-height: 100%;
}