.Card{
    width:100%;
    position: relative;
    max-height: 100vh;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}
.Dark{
    background-color: var(--primaryBColor);
    color:var(--secondaryBColor);
    border-color: var(--secondaryBColor) ;
}
.Light{
    background-color: #dbdbdb;
    color: var(--primaryBColor);
    border-color: var(--primaryBColor);
}
.Content{
    width: 50%;
    margin:0;
    min-height: 100vh;
    padding: 5vh 5%;
    padding-top: 30vh;
}
.Animate{
    width:100%;
    position: relative;
    min-height: 3rem;
    overflow: hidden;
    
}

.Animate div{
    position: absolute;
    width: 100%;
    background-color: var(--primaryBColor);
    min-height: 2.9rem;
    visibility: hidden;
    transform: translateY(0);
  
}
.Animate h4{
    font-size: 2.2rem;
    font-weight: 900;
    margin-bottom: 24px;
    position: absolute;
    overflow:hidden;
   

   
}

.Text{
    width: 100%;
    /* display: flex;
    
    align-items: flex-start; */
    margin: 0;
    margin-top: 24px;
    
   
    
}
.Text p{
    font-size: 1.1rem;
    line-height: 1.4rem;
    margin-left: -16px;
    max-width: 30rem;
   
}
.Text p span{
    display: block;
    margin-top: 12px;
}

.Text p a{
    color: var(--secondaryColor);
    text-decoration: none;
    display: inline-block;
}
.Text p a:hover, .Text p a:active{
    color: var(--primaryColor);
    transform: scale(1.05,1.05);
}
.Shape{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    float: left;
    
}
.Shape div:first-child{
    width: 60px;
    min-height: 2px;
    background-color: var(--secondaryColor);
    display: inline-block;

}
.Shape div:last-child{
    width: 50px;
    height: 50px;
    background-color: transparent;
    border: 1px solid ;
    border-radius: 50%;
    position: relative;
    left: -25px;
    top: 0px;
    display: inline-block;
    margin-right: 0px;
}

.Center{
    position: absolute;
    width:140px;
    height: 140px;
    bottom:3.5vh;
    left: calc(50% - 20px);
    transform: rotate(90deg) scale(1.0,1.0);
    text-align: center;
    padding: 24px;
    padding-top: 48px;
    z-index: 30;
    cursor: pointer;
    font-size: 1.3rem;
    
    
}
.Center:hover .Line{
    transform-origin: 100% 0%;
    transform:  scale(2.0,1.0);
    
}
.Center a{
    text-decoration: none;
    color:inherit;
}
.Line{
    min-height: 1px;
    transform-origin: 100% 0%;
    display: inline-block;
    width: 70px;
    position: absolute;
    left:70px;
    bottom:19px;
    transform:  scale(1.0,1.0);
    transition: transform 500ms ease-in-out ;
    
    
}

.Img{
    width: 50%;
    background-color: var(--primaryBColor);
    background-position: 0% 0%;
    background-size: 1280px 100vh;
    background-repeat: no-repeat;
    height: 100vh;
    margin:0;
    animation: moveImage 10s linear 500ms infinite alternate both;
}
@keyframes moveImage {
    from{
        background-position: 0% 0%;
    }
    to{
        background-position: 100% 0%;
    }
}

@media (max-width: 960px) {
    .Card{
        flex-direction: column;
        align-items: stretch;
        min-height: 100vh;

        /* max-height: 200vh; */
       
    }
    .Content{
        width:100%;
        max-height: 60vh;
        min-height: 54vh;
        padding-top: 5vh;
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
    }
    .Center{
        transform: rotate(0deg);
        top: 52vh;
        width: 90px;
        height: 90px;
        font-size: 0.8rem;
        padding: 12px;
    }
    .Line{
        display: none;
    }
    .Img{
        width: 100%;
        min-height: 40vh;
        height: 40vh;
        max-height: 50vh;
        background-size: 100% 40vh;
        animation: none;
        border-bottom-left-radius: 12px;
        border-bottom-right-radius: 12px;
    }
}
