.Section{
    background-color: var(--primaryBColor);
    background-position: 50% 50%;
    background-size: cover;
    background-blend-mode: multiply;
    color: var(--secondaryBColor);
    padding: 140px 10%;
    min-height: 60vh;
    width: 100%;
}
.Section h2{
    width: 100%;
    font-size: 3.7rem;
    font-weight: bolder;
    letter-spacing: 0.3rem;
}
.Section p{
    margin-top: 12px;
    font-size: 2.2rem;
}
.Section span{
    display: inline-block;
    background-color: var(--primaryColor);
    padding: 0 8px;
    color: var(--primaryBColor);
    font-weight: 500;
    border-radius: 2px;
}
.Skill{
    padding: 36px 10%;
    width: 100%;
    background-color: var(--primaryBColor);
}
.Skill h3{
    width: 100%;
    text-align: left;
    padding: 36px 0px;
    display: inline-block;
    font-size: 2.4rem;
    font-weight: 700;
    color: var(--primaryColor)


}
.Skill h3::before, .Skill h3::after{
    content: '';
    width: calc(50% - 60px);
    min-height: 2px;
    background-color: var(--secondaryBColor);
    border-radius: 4px;
    display: inline-block;
    margin-bottom: 8px;
}
.Skill h3::before{
    margin-left: 0;
    margin-right: 9px;
}
.Skill h3::after{
    margin-right: 0;
    margin-left: 9px;
}

.Grid{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.Grid>div{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 32px;
    margin-top: 32px;

}
@media (max-width: 600px) {
    .Section {
        padding: 140px 24px;
        min-width: 100%;
        box-sizing: border-box;
        margin-right: 0px;
        overflow-x: hidden;
    }
    .Section h2{
        font-size: 1.7rem;
        letter-spacing: normal;
    }
    .Section p{
        font-size: 1.1rem;
        line-height: 1.8rem;
    }
    .Grid{
        align-items: flex-start;
        justify-content: flex-start;
        
    }
    .Grid>div{
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        padding: 2px;
        width: 100%;
    }
}
